<template>
  <div id="section_2" class="sections">
    <div class="text_wrap">
      <div class="text_line" v-if="this.sec1Data.SalarySelect === 'ÏõîÍ∏â'">
        <h1>Ïò§ÎäòÍπåÏßÄ Î≤à ÎèàÏùÄ {{ salaryWon() }}Ïõê</h1>
      </div>
      <div class="text_line" v-else-if="this.sec1Data.SalarySelect === 'Ïó∞Î¥â'">
        <h1>
          Ïò§ÎäòÍπåÏßÄ Î≤à ÎèàÏùÄ
          <h1 class="Mfont">{{ salaryWon() }}</h1>Ïõê
        </h1>
      </div>
      <div class="text_line">
        <h1>
          ÏõîÍ∏âÍπåÏßÄ ÎÇ®ÏùÄÎÇ†ÏùÄ
          <small class="Mfont">{{ getNextSalaryDate() }}</small>Ïùº
        </h1>
      </div>
      <div class="text_line percent_wrap">
        <div class="percent" :style="{ width: percent + '%' }">
          <img v-if="percent < 50" src="../image/icon2.png" alt="ÏïÑÏù¥ÏΩò " />
          <img v-else src="../image/icon.png" alt="ÏïÑÏù¥ÏΩò " />
        </div>
        <div class="percent"></div>
      </div>
      <div class="text_line">
        <span @click="moveSection('section_3')" style="margin-top:35px">Ïù¥Í±∞ÏÇ¨Î†§Î©¥ ÏñºÎßàÎÇò Î≤åÏñ¥ÏïºÌïòÎäîÏßÄ ÏïåÏïÑÎ≥¥Í∏∞! ></span>
      </div>
    </div>
  </div>
</template>

<script>
import { moveTo } from "../utils/utils";
export default {
  data() {
    return {
      percent: "",
      dailyIncomePrice: "",
      minusDate: ""
    };
  },
  props: ["sec1Data"],
  methods: {
    moveSection(goto) {
      moveTo(goto);
    },
    getNextSalaryDate() {
      let result;
      const { payday } = this.sec1Data;
      const year = new Date().getFullYear();
      const date = new Date().getDate();
      const month = new Date().getMonth();
      const paydayDate = new Date(year, month + 1, payday);
      const paydayDates = new Date(year, month, payday);

      const todayDate = new Date(year, month, date);

      if (payday - new Date().getDate() >= 0) {
        //ÏõîÍ∏âÎÇ†ÏßúÍ∞Ä Ïò§Îäò ÎÇ†ÏßúÎ≥¥Îã§ Ïà´ÏûêÍ∞Ä ÌÅ¥Îïå
        result =
          (paydayDates.getTime() - todayDate.getTime()) / 1000 / 60 / 60 / 24;
        this.percent = 100 - (result / 30) * 100;
        this.minusDate = result + 1; //ÌòÑÏû¨ ÎßàÏù¥ÎÑàÏä§Í∞íÏù∏Îç∞ , ÏõîÍ∏â Îã§ÏùåÎÇ†Î∂ÄÌÑ∞ Í≥ÑÏÇ∞Ïóê Ìè¨Ìï®Ìï¥ÏïºÌïòÎØÄÎ°ú ÌîåÎü¨Ïä§Î°ú ÌïòÎ£® ÎπºÏ§ÄÎã§
        return result;
      } else {
        //ÏõîÍ∏âÎÇ†ÏßúÍ∞Ä Ïò§Îäò ÎÇ†ÏßúÎ≥¥Îã§ Ïà´ÏûêÍ∞Ä ÏûëÏùÑÎïå (Ï¶â ÏõîÍ∏â Î¨¥Ï°∞Í±¥ Îã§ÏùåÎã¨)
        result =
          (paydayDate.getTime() - todayDate.getTime()) / 1000 / 60 / 60 / 24;
        this.percent = 100 - (result / 30) * 100;
        return result;
      }
    },
    salaryWon() {
      const { payday, salary } = this.sec1Data;
      let { dailyIncomePrice } = this;
      let result;
      const date = new Date().getDate();
      const month = new Date().getMonth();
      const lastDate = new Date(2020, month + 1, 0).getDate();

      dailyIncomePrice = salary / lastDate;

      if (!this.minusDate) {
        this.$emit("postIncomePirce", dailyIncomePrice);
        result = Math.floor(
          parseFloat(dailyIncomePrice * (date - payday))
        ).toLocaleString();
        return result;
      } else {
        //ÏõîÍ∏âÎÇ†Ïù¥ ÎßàÏù¥ÎÑàÏä§ Îú∞Îïå
        const countWorkDate = -1 + date;
        result = Math.floor(
          parseFloat(dailyIncomePrice * countWorkDate)
        ).toLocaleString();
        return result;
      }

      /*  ---Ïó∞Î¥âÍ≥ÑÏÇ∞ÌïòÍ∏∞--- else {
        result = Math.floor(
          parseFloat((salary / 365) * countDate)
        ).toLocaleString();
        console.log(salary / 365, 'ÏÉêÎü¨Î¶¨Î•º ÎÇòÎààÍ±∞');
        console.log(countDate, 'countDate');
        return result;
      } */
    }
  }
};
</script>

